body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

html{
height: 100%;
}

#root{
	height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Poiret One';
  height: 100%
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navbar-brand {
    display: inline-block;
    padding-top: .3125rem;
    padding-bottom: .3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    line-height: inherit;
    font-weight: 600;
    white-space: nowrap;
    margin-left: 40px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.banner-holder{
	width: 100%;
	height: 73.6%;
	
	
	/* Create the parallax scrolling effect */
 background-attachment: fixed;
 background-position: center;
 background-repeat: no-repeat;
 background-size: cover;
}

.banner-tint{
	width: 100%;
	position: absolute;
	height: 72%;
	background: #000000aa;
	text-align: center;
}

.banner-name{
	color: white;
	font-size: 100px;
	margin-top: 6%
}

.banner-designation{
	color: white;
	font-size: 40px;
}

.banner-dp{
	height: 250px;
	width: 250px;	
	background-repeat: round;
	border-radius: 150px;
	border : 8px solid white;
	display: block;
	margin: auto;
	box-shadow: 2px 3px 1px 1px #2076d4;
}

.banner-dp-holder{
	width: 100%;
	text-align: center;
	position: absolute;
	top: 53%
}

.carousel-indicators>li{
    background: #2478d5;
    border-radius: 150px;
    width: 15px;
    height: 14px;
    border: 1px solid #ffffff;
}

.carousel-indicators>.active{
    background: #2478d5;
    border-radius: 150px;
    width: 15px;
    height: 14px;
    border: 2px solid #fb8989;
}

.details-item-holder{
	margin: 0px 100px 50px 100px;
}

.details-duration{
	font-family: 'Concert One';
    font-weight: 300;
    font-size: 20px;
    margin-top: 5px;
    margin-right: 5px
}

.details-item-title-text{
	color: black;
    font-size: 35px;
    border-bottom: 2px #fb8989 solid;
    font-weight: 800;
    border-collapse: separate;
}

.details-item-description{
	font-size: 19px;
    color: black;
    font-weight: 500;
    margin-top: 10px;
    text-align: justify;
    margin-bottom: 50px;
    margin-left: 10px;
    margin-right: 10px;
}


.skills-holder{
  background: url('/assets/projects-back.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 800px;
  position: relative;
}

.skill-tile-holder{
	background: #ffffff

}
.contact-container{

}

.quick-id-container{
  margin-left: 50px;
  min-height: 450px;
}

.quick-id-table{
  margin-top: 50px;
  margin-left: 40px;
}

.contact-field , .contact-field:active , .contact-field:focus{
  background: #bbc8dd;
  color: black;
  border: unset;
  border-bottom: 1px solid black;
  padding: 10px;
  outline: unset;
  width: 100%;
  margin-top: 20px
}

.row:{
  margin:0px
}

.submit-button{
  width: 100%;
  text-align: center;
  color: black;
  margin-top: 30px;
  font-size: 25px;
  border : 0.5px solid black
}

.submit-button:hover{
  cursor: pointer;
}

.project-tile{
  width: 90%;
  min-height: 300px;
  background: white;
  margin-bottom: 20px;
  margin-top: 20px;
  position: relative;
  box-shadow:2px 2px 14px 1px white
}

.project-title-text{
  color: white;
  margin-left: 10px;
  margin-top: 10px;
  font-size: 30px;
  position: absolute;
  top: 10px;
  left: 10px
}

